.container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 3rem;

  padding: 1rem 3rem;

  animation: fadein 2s;
  -moz-animation: fadein 2s; /* Firefox */
  -webkit-animation: fadein 2s; /* Safari and Chrome */
  -o-animation: fadein 2s; /* Opera */
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadein {
  /* Firefox */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  /* Safari and Chrome */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  /* Opera */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.container .headerSection {
  flex: 100%;
  height: 4rem;
  margin-bottom: -2rem;

  background: white;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  border-radius: 2rem 2rem 0.5rem 0.5rem;
}

.container .mainSection {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  gap: 3rem;
}

.container .mainSection .metaSection {
  flex: 20%;

  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
}

.container .mainSection .metaSection .moviesSection {
  flex: 30%;

  background: white;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  border-radius: 2rem;

  display: flex;
  align-items: center;
  justify-content: center;
}

.container .mainSection .metaSection .playListSection {
  flex: 60%;

  background: white;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  border-radius: 2rem;
}

.container .mainSection .playingVideoSection {
  flex: 40%;

  background: white;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  border-radius: 2rem;

  display: flex;
  align-items: center;
  justify-content: center;
}

.container .footerSection {
  flex: 100%;
  height: 4rem;
  margin-top: -2rem;

  background: white;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  border-radius: 0.5rem 0.5rem 2rem 2rem;
}
