.playlist {
  width: 80%;
  margin: auto;

  /* Make long list scrollable so that the list section doesn't stretch too far down */
  height: 100%;
  overflow-y: auto;
}

.playlist .title {
  margin-top: 1rem;
  text-align: center;
}

.playlist .videoTitle {
  margin-top: 1rem;
  padding-left: 1rem;
}

.playlist > p {
  padding: 1rem;
  color: var(--primary-green);
}

.playlist .videos {
  height: 70%;
  overflow-y: auto;
}

.playlist .videos .video {
  position: relative;
  width: 100%;
  height: 4rem;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 0 1rem;
  margin-top: 0.1rem;
  cursor: pointer;

  border-radius: 0.5rem;
}

.playlist .videos .video:hover {
  background-color: #0003;
}

.playlist .videos .video.active {
  background-color: #0003;
  color: black;
}

.playlist .videos .video.active img {
  filter: invert(100%) sepia(100%) saturate(2000%) hue-rotate(360deg);
}
