.playingVideo {
  width: 85%;
}

.playingVideo .title {
  margin-top: 1rem;
  text-align: center;
}

.playingVideo video {
  width: 100%;
  margin-top: 1rem;
  border-radius: 2rem;
}

.playingVideo iframe {
  width: 100%;
  height: 700px;
  margin-top: 1rem;
  border-radius: 2rem;
}

.playingVideo .transcriptTitle {
  color: var(--primary-green);
  margin-top: 1rem;
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 1rem;
}

.playingVideo .transcript {
  color: #000;
  margin-top: 1rem;
  font-size: 20px;
  /*font-family: BreitkopfFraktur;*/
  margin-bottom: 1rem;

  /* Make long transcript scrollable so that the transcript section doesn't stretch too far down */
  height: 300px;
  overflow-y: auto;
}

.video img {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);

  width: 1.5rem;
  height: 1.5rem;

  filter: invert(100%);
}

.video p {
  margin-left: 2.5rem;
}

.video h3 {
  width: 23rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  padding: 0 0.5rem;
}
